import React, { useEffect, useState } from 'react'
import { CButton, CCol, CFormInput, CRow, CForm } from '@coreui/react'

import './clinic.scss'
import { useNavigate } from 'react-router-dom';

const AddClinic = () => {
  const Navigate = useNavigate()
  const [doctor, setDoctor] = useState([]);
  const [error, setError] = useState({});
  const handelSubmit = (e)=>{
    e.preventDefault();
    console.log(e);
    const [clinic_name, registrationNo, clinicPhoneNo, clinicEmail, clinicLatLong, clinicAddress, establishmentDate, first_name, last_name, ownerPhoneNo, password] = e.target;
    
    const data = {
      clinic_name: clinic_name.value,
      registration_no: registrationNo.value,
      clinic_phone: clinicPhoneNo.value,
      clinic_email: clinicEmail.value,
      lat:clinicLatLong.value.split(",")[0],
      long: clinicLatLong.value.split(",")[1],
      address: clinicAddress.value,
      city:"",
      state:"",
      zip:"",
      country:"",
      est_date: establishmentDate.value,
      first_name: first_name.value,
      last_name:last_name.value,
      mobile: ownerPhoneNo.value,
      password: password.value,      
    };
    const url = process.env.REACT_APP_API_URL+"clinic/create";
    console.log(handelValidation(data))
    if(!handelValidation(data)){
    fetch(url, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(data)
    })
    .then(response => {
      return response.json();
    })
    .then(data => {
      console.log('Success:', data);
      if(data.status==200){
        Navigate("/clinic");
      }
    })
    .catch(error => {
      console.error('Error:', error);
    });
    }
  }
  const handelValidation = (arg)=>{
    const error = {};

    let  hasError = false;
    if(!arg.mobile){
      error.ownerPhoneNo = "Please enter a owner mobile no.";
      hasError = true;
    }
    if(!arg.password){
      error.password = "Please enter password.";
      hasError = true;
    }
    if(!arg.clinic_name){
      error.clinic_name = "Please enter clinic name.";
      hasError = true;
    }
    if(!arg.registration_no){
      error.registrationNo = "Please enter registration no.";
      hasError = true;
    }
    if(!arg.clinic_phone){
      error.clinicPhoneNo = "Please enter clinic phone no.";
      hasError = true;
    }
    if(!arg.first_name){
      error.first_name = "Please enter owner first name.";
      hasError = true;
    }
    if(!arg.last_name){
      error.last_name = "Please enter owner last name.";
      hasError = true;
    }
    setError(error);
    console.log(arg, error)
    return hasError;
  }

  useEffect(() => {
    
  }, [])

  return (
    <div className="addFormGlobal">
      <h2>Add Clinic</h2>
      <CForm onSubmit={handelSubmit}> 
        <CRow>
          <CCol lg="6">
            <label>Clinic Name</label>
            <CFormInput placeholder="" name='clinic_name'/>
            <div className='errorMsg'>{error.clinic_name}</div>
          </CCol>
          <CCol lg="6">
            <label>Registration No</label>
            <CFormInput placeholder="" name='registrationNo'/>
            <div className='errorMsg'>{error.registrationNo}</div>
          </CCol>
          <CCol lg="6">
            <label>Clinic Phone no</label>
            <CFormInput placeholder="" name='clinicPhoneNo' />
            <div className='errorMsg'>{error.clinicPhoneNo}</div>
          </CCol>
          <CCol lg="6">
            <label>Clinic Email</label>
            <CFormInput placeholder="" name='clinicEmail' />
            <div className='errorMsg'>{error.clinicEmail}</div>
          </CCol>
          <CCol lg="6">
            <label>Clinic Lat long</label>
            <CFormInput placeholder="" name='clinicLatLong'/>
            <div className='errorMsg'>{error.clinicLatLong}</div>
          </CCol>
          <CCol lg="6">
            <label>Clinic Address</label>
            <CFormInput placeholder="" name='clinicAddress'/>
            <div className='errorMsg'>{error.clinicAddress}</div>
          </CCol>
          <CCol lg="4">
            <label>Establishment Date</label>
            <CFormInput placeholder="" type="date" name='establishmentDate'/>
            <div className='errorMsg'>{error.establishmentDate}</div>
          </CCol>
          <CCol lg="4">
            <label>Owner First Name</label>
            <CFormInput placeholder="" name='first_name'/>
            <div className='errorMsg'>{error.first_name}</div>
          </CCol>
          <CCol lg="4">
            <label>Owner Last Name</label>
            <CFormInput placeholder="" name='last_name'/>
            <div className='errorMsg'>{error.last_name}</div>
          </CCol>
          <CCol lg="6">
            <label>Owner Phone no</label>
            <CFormInput placeholder="" name='ownerPhoneNo'/>
            <div className='errorMsg'>{error.ownerPhoneNo}</div>
          </CCol>
          <CCol lg="6">
            <label>Set a Password</label>
            <CFormInput placeholder="" name='password'/>
            <div className='errorMsg'>{error.password}</div>
          </CCol>
          <CCol lg="12" className="text-center">
            <CButton type='submit' size="lg">Add Clinic</CButton>
          </CCol>
        </CRow>
      </CForm>
    </div>
  )
}
export default AddClinic
